@import url(https://fonts.googleapis.com/css?family=Nunito:600,700&display=swap);
body {
  margin: 0;
  font-family: Nunito, Roboto, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(#608FDA, #A5B6F0);
  height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container{
  text-align: center;
  margin-top: 100px;
}

.container header{
  font-size: 1.3rem;
  margin: 0 auto 60px auto;
  position: relative;
  height: 25px;
  width: 250px;
  overflow: hidden;
}

.container p {
  font-size: 12px;
  padding: 10px;
}

.container input {
  border: 10px;
  margin-bottom: 10px;
  padding: 12px;
  font-size: 1rem;
  color: #000;
  border-radius: 5px;
}

.container button {
  border: 10px;
  padding: 5px;
  font-size: 14px;
  color: #000;
  border-radius: 5px;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

